<template>
  <div class="d-flex justify-center flex-wrap">
    <div style="flex: 0 0 100%">
      <v-chip label :ripple="false">{{ className}}</v-chip>
      <span class="ml-4">請選擇執行對象</span>
      <v-checkbox
        class="d-inline-block float-right"
        :label="'全選'"
        @change="onChangeSelectAll($event)"
      ></v-checkbox>
    </div>
    <div class="mt-6 d-flex flex-wrap justify-space-between " style="flex: 0 0 100%">
      <v-card class="my-2" v-for="(classmate,index) in classmates" :key="index">
        <v-avatar
          @click="changeStatus(classmate)"
          :color="classmate.selected?'primary':'grey'"
          :size="90"
        ></v-avatar>
        <div>{{ classmate.name }}</div>
      </v-card>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      className: "小熊搖籃幼幼班",
      classmates: [
        { name: "寶寶1", selected: false },
        { name: "寶寶2", selected: false },
        { name: "寶寶3", selected: false },
        { name: "寶寶4", selected: false },
        { name: "寶寶5", selected: false },
        { name: "寶寶6", selected: false },
        { name: "寶寶7", selected: false },
      ],
    };
  },
  methods: {
    changeStatus(mate) {
      mate.selected = !mate.selected;
    },
    onChangeSelectAll($event) {
      this.classmates.forEach((c) => (c.selected = $event));
    },
  },
};
</script>

